@import url('https://fonts.googleapis.com/css?family=Audiowide');

.body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Audiowide';
}

.page-display {
    width: 100%;
    height: 100%;
    background-color: #0d1019;
}

.body-container {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 88vh;
    color: white;
}

.data-grid {
    height:82.5vh;
    width: 98%;
    padding: 1%;
}

.bottom-bar{
    position : relative;
    height : 6.1vh;
}

.footer{
    position : absolute;
    bottom : 0;
}

.footerContainer {
    color: white;
}

.submit-button {
    width: auto;
    height: 100%;
    font-size: 1.85vh;
    font-weight: 100;
    padding: 1vh;
    padding-left: 3vh;
    padding-right: 3vh;
    border-radius: 3vh;
    margin-top: 0.5vh;
    color: white;
    border: .05vh solid #0984e3;
    background-color: #353b48;
    font-family: "audiowide";
}

.remove-button {
    width: auto;
    height: 100%;
    font-size: 1.85vh;
    font-weight: 100;
    padding: 1vh;
    padding-left: 3vh;
    padding-right: 3vh;
    border-radius: 3vh;
    margin-top: 0.5vh;
    color: white;
    border: .05vh solid red;
    background-color: #353b48;
    font-family: "audiowide";
}





